import React,{Component} from 'react';
import '../css/add.css';
import { EditorState, convertToRaw, ContentState} from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import draftToHtml from 'draftjs-to-html';
import '../public/public'
import htmlToDraft from 'html-to-draftjs';

function Edits(props){
    function url_admin(e){
        e.stopPropagation();
        props.my.props.history.push('/admin');
    }
    function cate(e){
       props.my.setState({
           cateid:e.target.value
       })
    }
    function title(e){
        props.my.setState({
            title:e.target.value
        })
    }
    function source(e){
        props.my.setState({
            source:e.target.value
        })
    }
    function brief(e){
        props.my.setState({
            brief:e.target.value
        })
    }
    function content(editorState){
        props.my.setState({
            content:draftToHtml(convertToRaw(editorState.getCurrentContent())),
            editorState
        })
    }
    function save(e){
        props.my.setState({
            msg:''
        })
        e.stopPropagation();
        let art = {
            title:props.my.state.title,
            cateid:props.my.state.cateid,
            source:props.my.state.source,
            brief:props.my.state.brief,
            content:props.my.state.content,
            par:props.my.state.par
        };
        
        if(empty_fun(art)){
            props.my.setState({
                msg:'输入不能为空'
            })
            return false;
        }

        if(!props.my.state.click_stu){
            props.my.state.click_stu = true;
            global.public.server('article','PUT',JSON.stringify(art),function(data){
                if(global.public.is_ObjKeyEmpty('edit',data)&&data.edit){
                    props.my.state.click_stu = false;
                    global.public.msg('/admin','修改成功',1,props.my);
                }else{
                    global.public.msg('/admin','修改失败',3,props.my);
                }
            });
        }
        function empty_fun(obj={}){
            for(let v in obj){
                if(obj[v].length===0){
                   return true;
                }
            }
            return false;
        }
    }
    let form = <div className='add'>
       <table cellSpacing="30">
           <tbody>
               <tr>
                   <td className='input_tit'>标题</td>
                   <td><input type='text' onChange={title} className='input_text' value={props.my.state.title} /></td>
                </tr>
                <tr>
                   <td className='input_tit'>来源</td>
                   <td><input type='text' onChange={source} className='input_text' value={props.my.state.source} /></td>
                </tr>
                <tr>
                   <td className='input_tit'>栏目</td>
                   <td>
                        <select name="cars" onChange={cate}>
                            <option value="1">cate1</option>
                            <option value="1">cate2</option>
                            <option value="1">cate3</option>
                        </select>
                   </td>
                </tr>
                <tr>
                   <td className='input_tit'>摘要</td>
                   <td><input type='text' onChange={brief} className='input_text' value={props.my.state.brief}/></td>
                </tr>
                <tr>
                   <td className='input_tit'>内容</td>
                   <td>        
                        <Editor
                            editorState={props.my.state.editorState}
                            toolbarClassName="toolbarClassName"
                            wrapperClassName="wrapperClassName"
                            editorClassName="editorClassName"
                            onEditorStateChange={content}
                        />
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td><span className='msg'>{props.my.state.msg}</span></td>
                </tr>
                <tr>
                    <td></td>
                    <td className='btn' onClick={save}><button>发布</button><button onClick={url_admin}>返回</button></td>
                </tr>
           </tbody>
       </table>
    </div>;
    return form;
}

class Edit extends Component {
    constructor(props){
        super(props);
        this.state = {
            editorState: EditorState.createEmpty(),
            title:'',
            cateid:1,
            source:'',
            brief:'',
            content:'',
            click_stu:false,
            msg:'',
            par:''
        }
    }

    componentWillMount(){
        let my =  this;
        let aid = this.props.match.params.aid.split(':')[1];
        if(aid){          
            global.public.server('article','GET',aid,function(data){
                my.setState({
                    title:data.title,
                    cateid:data.cateid,
                    source:data.source,
                    brief:data.brief,
                    content:data.content,
                    par:aid
                })
                //给文本编辑器赋值
                const contentBlock = htmlToDraft(data.content);
                const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
                const editorState = EditorState.createWithContent(contentState);
                my.setState({ editorState })
            });
        }else{
            return false;
        }
    }

    render(){
        return (
            <Edits my={this} />
        )
    }
}

export default Edit;